"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Loader from "./";

export default function Nearby() {
  const [hotels, setHotels] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    async function fetchNearby() {
      if (!navigator.geolocation) {
        setError("Geolocation not supported.");
        setLoading(false);
        return;
      }

      navigator.geolocation.getCurrentPosition(
        async (pos) => {
          const lat = pos.coords.latitude;
          const lon = pos.coords.longitude;

          setLoading(true);
          setError("");

          try {
            interface Hotel {
              hotel_id?: string;
              id?: string;
              max_photo_url?: string;
              main_photo_url?: string;
              photo1?: string;
              image_url?: string;
              hotel_name?: string;
              name?: string;
              review_score?: number;
              city?: string;
            }

            const [hotels, setHotels] = useState<Hotel[]>([]);

            const res = await fetch(
              `/api/search-by-coordinates?lat=${lat}&lon=${lon}`,
              { cache: "no-store" }
            );

            const data = await res.json();
            const hotelsList =
              data.result || data.hotels || data.data || data.list || [];

            // ðŸ”¥ LIMIT RESULTS TO 3
            const limited = Array.isArray(hotelsList)
              ? hotelsList.slice(0, 3)
              : [];

            setHotels(limited);
          } catch (err) {
            console.error(err);
            setError("Failed to load nearby hotels.");
          } finally {
            setLoading(false);
          }
        },

        (err) => {
          console.error("GEO ERROR:", err.code, err.message);

          if (err.code === 1) setError("Location permission denied.");
          if (err.code === 2)
            setError("Location unavailable. Check macOS privacy settings.");
          if (err.code === 3) setError("Location request timed out.");

          setLoading(false);
        }
      );
    }

    fetchNearby();
  }, []);

  if (loading) return <p className=""><Loader/></p>;
  if (error) return <p className="text-red-500">{error}</p>;

  return (
    <div className="flex justify-center items-center flex-col mt-6">
      <h2 className="text-3xl text-black font-bold mb-6">Hotels Near You</h2>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {hotels.length === 0 && <p>No hotels found nearby.</p>}

        {hotels.map((hotel: any) => (
          <div
            key={hotel.hotel_id || hotel.id}
            className="bg-white rounded-xl shadow-lg p-8 border border-gray-100 hover:shadow-xl transition-shadow"
          >
            <div className="bg-blue-100 rounded-lg flex items-center justify-center mb-6">
              <img
                src={
                  hotel.max_photo_url ||
                  hotel.main_photo_url ||
                  hotel.photo1 ||
                  hotel.image_url ||
                  "/images/hotel-placeholder.jpg"
                }
                alt={hotel.hotel_name || hotel.name || "Hotel Image"}
                className="w-full h-48 object-cover rounded-lg"
              />
            </div>

            <h3 className="text-xl font-semibold text-gray-900 mb-3">
              {hotel.hotel_name || hotel.name || "Unnamed Hotel"}
            </h3>

            <p className="text-gray-600">
              Rating: {hotel.review_score || "N/A"}
            </p>
            <p className="text-gray-600">{hotel.city}</p>
          </div>
        ))}
      </div>

      {/* ðŸ”¥ SEE MORE BUTTON */}
      <Link
        href="/hotels"
        className="mt-10 inline-block bg-blue-600 text-white px-6 py-3 rounded-lg shadow hover:bg-blue-700 transition"
      >
        See More Hotels
      </Link>
    </div>
  );
}
